@keyframes floatUp {
    0% {
      opacity: 0;
      transform: translateY(50%);
    }
    100% {
      opacity: 1;
      transform: translateY(0);
    }
  }
  
  .float-up {
    animation-name: floatUp;
    animation-duration: 1s;
    animation-delay: 0.5s;
    animation-fill-mode: both;
  }
  